* {
  font-family: Arial, Helvetica, sans-serif;
}
body {
  padding: 60px;
}

input {
  color: grey;
  font-size: medium;
  width: 100%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;



}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;

}

.card p {
  padding: 5px;
  text-align: center;

}
.card img {
  max-height: 170px ;
}